@use 'global' as *;

.introduction {

}

.introduction__inner {

}

.introduction__title-area {

}

.introduction__title--ja{

}

.introduction__title--en {

}

.introduction__whole-wrapper{

}

.introduction__container {
    display: flex;
}

.introduction__container-even {
    display: flex;
    flex-direction: row-reverse;
}

.introduction__each-wrapper {
    margin-top: rem(213);
    // border-bottom: 1px solid $white;
}    

.introduction__image-big {
    width: 100%;
    max-width: rem(649);
    position: relative;
}
.introduction__image-big::before {
    content:"";
    display: block;
    padding-top: 100%; 
}
.introduction__image-big img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.introduction__room-name {
    margin-top: rem(288);
    margin-left: rem(74);
    writing-mode: vertical-rl;
    font-size: rem(62);
    letter-spacing: 0.5em;
}

.introduction__image-wrapper {
    margin-top: rem(-174);
    margin-left: rem(108);
    width: rem(380);
}

.introduction__image-small {
    width: 100%;
    position: relative;
}
.introduction__image-small::before {
    content:"";
    display: block;
    padding-top: 135%; 
}
.introduction__image-small img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.introduction__person {
    margin-top: rem(58);
    text-align: center;
    font-size: rem(26);
    position: relative;
}

.introduction__person::after{
    position: absolute;
    bottom: -23px;
    left: 43%;
    content: "";
    width: 61px;
    border-bottom: 1px solid yellow;
}

.introduction__capacity {
    margin-top: rem(48);
    text-align: center;
    font-size: rem(20);
}

.introduction__breadth {
    margin-top: rem(8);
    text-align: center;
    font-size: rem(20);
}
.introduction__text-area {
    margin-top: rem(128);
    margin-left: auto;
    margin-right: auto;
    max-width: rem(1076);
    width: 100%;
    background-color: $white;
    color: $black;
}
.introduction__text {
    padding: rem(53) rem(53);
    line-height: 1.5;
}
.introduction__button-area {
    margin-top: rem(55);
    text-align: center;
}
